import store from 'store'
const Mock = require('mockjs')
import dayjs from 'dayjs'
import { Toast } from 'vant'
import onfire from "onfire.js";

const Random = Mock.Random
const staffMockList = [
  { name: '', short: 'zc', id: 1 },
  { name: '', short: 'lk', id: 2 },
  { name: '', short: 'wsl', id: 3 },
  { name: '', short: 'dz', id: 4 },
  { name: '', short: 'xm', id: 5 },
  { name: '', short: 'hyt', id: 6 },
  { name: '', short: 'am', id: 8 },
  { name: '', short: 'kk', id: 9 },
  { name: '', short: 'wh', id: 10 },
  { name: '', short: 'zld', id: 11 },
  { name: '', short: 'lm', id: 12 },
  { name: '', short: 'tt', id: 13 },
  { name: '', short: 'wax', id: 14 },
  { name: '', short: 'xs', id: 15 },
  { name: '', short: 'wll', id: 16 },
  { name: '', short: 'zl', id: 17 },
  { name: '', short: 'hdl', id: 18 },
  { name: '', short: 'ay', id: 19 },
  { name: '', short: 'cyn', id: 20 }
]
const itemTypeMockList = [
  { name: '', price: Random.natural(20, 25) },
  { name: '', price: Random.natural(15, 20) },
  { name: '', price: Random.natural(5, 15) },
  { name: '', price: Random.natural(60, 100) },
  { name: '', price: Random.natural(5, 10) },
  { name: '', price: Random.natural(15, 35) },
  { name: '', price: Random.natural(20, 30) },
  { name: '', price: Random.natural(3, 13) },
  { name: '', price: Random.natural(20, 60) },
  { name: '', price: Random.natural(100, 200) }
]

export default function () {
  let randomNumA = 0
  let randomNumB = 0
  let randomNumC = 0
  let randomNumD = 0
  const STAFF_LIST = []
  for (let i = 1; i <= Random.natural(6, 10); i++) {
    STAFF_LIST.push(staffMockList.splice(Random.natural(0, staffMockList.length - 1), 1)[0])
  }
  const ITEM_TYPE_LIST = []
  for (let i = 1; i <= Random.natural(4, 8); i++) {
    ITEM_TYPE_LIST.push(itemTypeMockList.splice(Random.natural(0, itemTypeMockList.length - 1), 1)[0])
  }
  const OPERATION_RECORD_LIST = []
  // 
  for (let i = 1; i <= Random.natural(10, 30); i++) {
    OPERATION_RECORD_LIST.push({
      type: ITEM_TYPE_LIST[Random.natural(0, ITEM_TYPE_LIST.length - 1)].name,
      staff: STAFF_LIST[Random.natural(0, STAFF_LIST.length - 1)].name,
      num: Random.natural(1, 20),
      time: dayjs().subtract(Random.natural(randomNumA, randomNumA += Random.natural(0, 60)), 'minute'),
      action: 'PIECE_RECORD',
      actionName: ''
    })
  }
  // 
  for (let i = 1; i <= Random.natural(60, 200); i++) {
    OPERATION_RECORD_LIST.push({
      type: ITEM_TYPE_LIST[Random.natural(0, ITEM_TYPE_LIST.length - 1)].name,
      staff: STAFF_LIST[Random.natural(0, STAFF_LIST.length - 1)].name,
      num: Random.natural(1, 20),
      time: dayjs().subtract(Random.natural(randomNumB, randomNumB += Random.natural(0, 3)), 'day'),
      action: 'PIECE_RECORD',
      actionName: ''
    })
  }
  // 
  for (let i = 1; i <= Random.natural(5, 15); i++) {
    OPERATION_RECORD_LIST.push({
      type: ITEM_TYPE_LIST[Random.natural(0, ITEM_TYPE_LIST.length - 1)].name,
      num: Random.natural(10, 20),
      time: dayjs().subtract(Random.natural(randomNumC, randomNumC += Random.natural(0, 60)), 'minute'),
      action: 'GOODS_EXPORT',
      actionName: ''
    })
  }
  // 
  for (let i = 1; i <= Random.natural(20, 30); i++) {
    OPERATION_RECORD_LIST.push({
      type: ITEM_TYPE_LIST[Random.natural(0, ITEM_TYPE_LIST.length - 1)].name,
      num: Random.natural(10, 40),
      time: dayjs().subtract(Random.natural(randomNumD, randomNumD += Random.natural(0, 3)), 'day'),
      action: 'GOODS_EXPORT',
      actionName: ''
    })
  }
  store.set('STAFF_LIST', STAFF_LIST)
  store.set('ITEM_TYPE_LIST', ITEM_TYPE_LIST)
  store.set('OPERATION_RECORD_LIST', OPERATION_RECORD_LIST)
  store.set('LATELY_ID', 20)
  Toast.success({
    message: '',
    title: ''
  })
  onfire.fire("reload");
}
